import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController } from 'ionic-angular';
import { AngularFirestore } from 'angularfire2/firestore';
import { Collections, Event } from '../../app/app.models';
import moment from 'moment'
import { AuthProvider } from '../../providers/auth/auth';

@IonicPage()
@Component({
  selector: 'page-calendar',
  templateUrl: 'calendar.html',
})
export class CalendarPage {

  public calendar: Event[] = []
  private uid: string = localStorage.getItem('uid')

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private load: LoadingController,
    private afs: AngularFirestore,
    private auth: AuthProvider,
  ) {
  }

  ionViewDidLoad() {

    const l = this.load.create({content: 'Cargando calendario...'})
    l.present()

    let loaded = false;

    this.auth.user$.subscribe(user => {
      if (user && !loaded) {

        this.afs.collection<Event>(Collections.EVENT)
          .valueChanges()
          .subscribe(events => {
            l.dismiss()
            this.calendar = events.filter((e: Event) => moment(e.date).startOf('day').format('DD-MM-YYYY') == moment().startOf('day').format('DD-MM-YYYY'))
            this.calendar = this.calendar.filter(e => {
              if (e.unlockedBy) {
                return user[e.unlockedBy];
              }
            })
            console.log(this.calendar)
          })

      }
    })

  }

  onChangeTask(completed: boolean, id: string, i: number, ii: number, evento: string) {
    if (completed) {
      localStorage.setItem(`evento-${evento}-${id}-${this.uid}`, `${completed}`)
    } else {
      localStorage.removeItem(`evento-${evento}-${id}-${this.uid}`)
    }
    this.calendar[ii].tareas[i].completed = completed
  }

  openVideo(c) {
    console.log(c);
    this.navCtrl.push('ClaseDetailPage', {id: c.link_video_1, type: c.type_video_1})
  }

  openSlide(c) {
    this.navCtrl.push('SlideDetailPage', {id: c.link_slide_1})
  }

}



